import React from 'react';
import './CreateTodoButton.css';

function CreateTodoButton(){
    const onCllicknoseque = (msg)=> {
        alert(msg + '')
    }
    return (
        <button className="CreateTodoButton"
        onClick={() => onCllicknoseque('Toque')}>
            +
        </button>
    );
}
export {CreateTodoButton};